.icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
}

.toast-notification {
    position: fixed;
    top: 20px;
    left: 0;
    right: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.toast-notice-wrapper.notice-enter {
    opacity: 0.01;
    -webkit-transform: scale(0);
        -ms-transform: scale(0);
            transform: scale(0);
}

.toast-notice-wrapper.notice-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    -webkit-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}

.toast-notice-wrapper.notice-exit {
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}

.toast-notice-wrapper.notice-exit-active {
    opacity: 0.01;
    -webkit-transform: translateY(-40%);
        -ms-transform: translateY(-40%);
            transform: translateY(-40%);
    -webkit-transition: all 300ms ease-out;
    -o-transition: all 300ms ease-out;
    transition: all 300ms ease-out;
}

.toast-notice {
    background: #FFFFFF;
    padding: 16px 32px;
    margin: 8px auto;
    -webkit-box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, .1);
            box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, .1);
    border-radius: 6px;
    color: #454545;
    font-size: 15px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.toast-notice>span {
    margin-left: 6px;
    line-height: 100%;
}

.toast-notice>svg {
    font-size: 18px;
}

.toast-notice.info>svg {
    color: #1890FF;
}

.toast-notice.success>svg {
    color: #52C41A;
}

.toast-notice.warning>svg {
    color: #FAAD14;
}

.toast-notice.error>svg {
    color: #F74A53;
}

.toast-notice.loading>svg {
    color: #1890FF;
    -webkit-animation: rotating 1s linear infinite;
            animation: rotating 1s linear infinite;

}

@-webkit-keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes rotating {
    0% {
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}


